body, html {
  margin: 0;
  padding: 0;
  width: 100%;
  height: auto; 
  overflow-x: hidden;
  font-family: Arial, sans-serif;
  box-sizing: border-box; 
  /* font-size: 16px; */
}
/* Styling for the loading screen */
.loading-screen {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  background-color: black;
}

.nike-logo {
  width: 200px;
  animation: blink 1s infinite;
}

@keyframes blink {
  0% {
      opacity: 0;
  }
  50% {
      opacity: 1;
  }
  100% {
      opacity: 0;
  }
}

/* Styling for the main page */
.main-page {
  display: flex;
  flex-direction: column;
  justify-content: flex-start; 
  align-items: center;
  min-height: 100vh;  
  background-color: #f8ecd7;
  text-align: center;
  margin: 0;
  padding: 20px; 
  box-sizing: border-box; 
  overflow: auto; 
}


/* Navbar styling */
.navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px 20px;
  background-color: #f8ecd7; 
  color: black;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 1000; 
}

.logo-container {
  display: flex;
  align-items: center;
}

.nike-logo-navbar {
  width: 80px;
  height: auto;
  margin-right: 20px;
}

.features {
  display: flex;
  margin-left: 40px;
  gap: 30px;
}

.feature {
  font-family: 'Nike', sans-serif;
  font-size: 35px;
  cursor: pointer;
  color: #131212;
  text-transform: uppercase;
  font-weight: 600;
  position: relative;
  padding: 20px 0;
}

.feature::before {
  content: '';
  position: absolute;
  top: 100%;
  left: 0;
  width: 100%;
  height: 20px; 
}

.dropdown {
  display: none;
  position: absolute;
  top: calc(100% + 20px); 
  left: -50%;
  background-color: #EEE1C6;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.1);
  border-radius: 8px;
  z-index: 100;
  min-width: 800px;
  padding: 30px;
}

/* Show dropdown on hover and keep it visible */
.feature:hover .dropdown,
.dropdown:hover {
  display: block;
}

.sub-dropdown {
  display: flex;
  gap: 40px;
  padding: 10px;
}

.category-section {
  flex: 1;
  min-width: 200px;
}

.category-title {
  font-size: 23px;
  font-weight: 700;
  color: #131212;
  margin-bottom: 15px;
  text-transform: uppercase;
  padding-bottom: 10px;
  border-bottom: 2px solid #f0f0f0;
}

.sub-dropdown-row {
  padding: 12px 0;
  font-size: 23px;
  color: #855E42;
  transition: all 0.2s ease;
  text-transform: none;
  position: relative;
  padding-left: 0;
}

.sub-dropdown-row:hover {
  color: #131212;
  padding-left: 5px; 
  background: linear-gradient(to right, #f8f8f8, transparent);
}

/* Add hover line effect */
.sub-dropdown-row::after {
  content: '';
  position: absolute;
  bottom: 0;
  left: 0;
  width: 0;
  height: 1px;
  background-color: #131212;
  transition: width 0.2s ease;
}

.sub-dropdown-row:hover::after {
  width: 100%;
}

.feature::after {
  content: '';
  display: inline-block;
  width: 6px;
  height: 6px;
  border-right: 2px solid #131212;
  border-bottom: 2px solid #131212;
  margin-left: 8px;
  transform: rotate(45deg);
  position: relative;
  top: -2px;
}

/* Smooth animation */
.dropdown {
  transform-origin: top center;
  animation: dropdownFade 0.2s ease;
}

@keyframes dropdownFade {
  from {
    opacity: 0;
    transform: translateY(-10px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.right-icons {
  display: flex;
  align-items: center;
}

.icon {
  width: 55px;
  height: auto;
  margin-left: 15px;
  cursor: pointer;
}

/* Search Bar */
.search-container {
  display: flex;
  align-items: center;
  position: relative;
}

.search-bar {
  width: 0;
  height: 16px;
  padding: 12px;
  border: 1px solid #a9a06c;
  border-radius: 20px;
  font-size: 14px;
  opacity: 0;
  visibility: hidden;
  transition: all 0.3s ease;
}

.search-bar.show {
  width: 150px;
  opacity: 2;
  visibility: visible;
}

.icon.search-icon {
  cursor: pointer;
}

/* User Dropdown */
.user-dropdown {
  display: none;
  position: absolute;
  top: 90px; /* Adjust based on your layout */
  right: 50px; /* Adjust based on your layout */
  background-color: #856842;
  border-radius: 8px;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.1);
  width: 210px;
  height: 200px;
  opacity: 0; /* Initially hidden */
  transform: translateY(-20px); /* Initially translate the dropdown upwards */
  transition: opacity 0.3s ease, transform 0.3s ease;
  z-index: 10; /* Ensure the dropdown appears above other elements */
}

/* Show dropdown when active */
.user-dropdown.show {
  display: block;
  opacity: 1; /* Make the dropdown visible */
  transform: translateY(0); /* Move the dropdown back to its position */
}

/* Dropdown List Style */
.user-dropdown ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

/* Dropdown List Item */
.user-dropdown li {
  padding: 20px 20px;
  border-bottom: 1px solid #ddd;
  transition: background-color 0.3s ease;
}

/* Remove border from last item */
.user-dropdown li:last-child {
  border-bottom: none;
}

/* Link Style */
.user-dropdown li a {
  text-decoration: none;
  color: #f9f7f6;
  font-size: 25px;
  display: block;
  transition: color 0.3s ease;
}

/* Hover effect on list items */
.user-dropdown li:hover {
  background-color: #e3cba3;
}

/* Hover effect on the link */
.user-dropdown li a:hover {
  color: #0c0c0c; /* Change to your desired color on hover */
}

/* User Icon */
.icon {
  position: relative; /* Ensure the icon is positioned within its container */
  z-index: 20; /* Make sure the icon is always above the dropdown */
  cursor: pointer; /* Add cursor to indicate it's clickable */
}

.icon:hover {
  opacity: 0.8; /* Slightly fade the icon on hover for effect */
}



/* Styling for the main content */
h1 {
  font-family: 'Nike', sans-serif;
  color: black;
  font-size: 3rem;
  margin-top: 100px; 
}

p {
  font-size: 2.5rem;
}

@media screen and (max-width: 768px) {
  /* Hide Men/Women options on small screens */
  .features {
      display: none;
  }

  /* Center align logo and features on mobile */
  .logo-container {
      flex-direction: column;
      align-items: center;
  }

  .nike-logo-navbar {
      width: 80px; 
  }

  /* Adjust right icons on mobile */
  .right-icons {
      display: flex;
  }

  .icon {
      width: 25px;
  }
}

@media screen and (max-width: 480px) {
  h1 {
      font-size: 2rem; 
  }

  p {
      font-size: 1.2rem;
  }

  .nike-logo-navbar {
      width: 60px;
  }

  .icon {
      width: 20px;
  }
}

/* Mobile Menu Styles */
.mobile-menu {
  position: fixed;
  top: 0;
  right: -300px;
  width: 300px;
  height: 60vh;
  background-color: #EEE1C6 ;
  z-index: 1000;
  transition: transform 0.3s ease-in-out;
  display: none;
}

.mobile-menu.open {
  transform: translateX(-300px);
}

.mobile-menu-header {
  padding: auto;
  border-bottom: 1px solid #e5e5e5;
  display: flex;
  justify-content: flex-end;
}

.close-button {
  background: none;
  border: none;
  font-size: 2rem;
  cursor: pointer;
  padding: 0.5rem;
  color: #111;
}

.mobile-nav {
  padding: 1rem 0;
}

.mobile-menu-item {
  padding: 1rem 1.5rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  cursor: pointer;
  font-size: 1rem;
  color: #111;
  border-bottom: 1px solid #7f4720;
}

.chevron {
  font-size: 1.2rem;
  color: #757575;
}

.mobile-menu-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 999;
  display: none;
}

@media (max-width: 768px) {
  .mobile-menu,
  .mobile-menu-overlay {
    display: block;
  }
  
  .features {
    display: none;
  }
}

.video-container {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 60vh;
  margin: 0;
  padding: 0;
  overflow: hidden;
}

.responsive-video {
  max-width: 100%;
  max-height: 100%;
  width: 100%;
  height: auto;
  object-fit: contain;
  border-radius: 10px;
}



@media (max-width: 768px) {
  .video-container {
    height: auto;  /* Allow for scaling on smaller screens */
    padding: 10px;  /* Add some space around the video */
  }

  .responsive-video {
    width: 100%;
    height: auto;
    object-fit: contain;
  }
}


.content {
  text-align: center;
  padding: 10px;
}

h4 {
  margin-top: 10px;
  font-family: Arial, sans-serif;
  font-size: 1em;
  color: #514e4e;
  margin-bottom: 0;
  text-transform: uppercase;
}

h4:nth-child(1) {
  font-family: 'Arial', sans-serif;
  font-size: 1em;
  color: #383434;
  text-transform: uppercase;
}

.title-container {
  font-family: Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
  font-size: 2.5em;
  color: #000000;
  text-transform: uppercase;
  line-height: 1;
  margin-top: 3px;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 1px;
  font-weight: 600;
}

.winning {
  display: block;
  font-weight: 600;
  font-family: Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
}

p {
  font-family: 'Arial', sans-serif;
  font-size: 1.5em;
  color: #383434;
  font-weight: 600;
  margin-top: 3px;
  text-transform: uppercase;
}

.shop-button {
  background-color: #111111;
  color: #fff;
  padding: 25px 40px; 
  border-radius: 25px; /* Slightly reduced from 30px */
  border: 2px solid #111;
  font-size: 14px; /* Reduced from 16px */
  font-weight: 500;
  cursor: pointer;
  transition: all 0.3s ease;
  text-transform: uppercase;
  letter-spacing: 1px;
  position: relative;
  overflow: hidden;
  z-index: 1;
}

/* Keep the same hover effects */
.shop-button::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 0;
  height: 100%;
  background-color: #fff;
  transition: all 0.3s ease;
  z-index: -1;
}

.shop-button:hover {
  color: #111;
  transform: translateY(-2px);
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.1);
}

.shop-button:hover::before {
  width: 100%;
}

/* Responsive design */
@media screen and (max-width: 480px) {
  .shop-button {
    padding: 10px 24px; 
    font-size: 12px;
  }
}

/* Image Carousel Container */
.image-carousel {
  margin-top: 20px;
  padding: 20px;
  background-color: #f8ecd7;
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 90%;
  box-sizing: border-box;
}

/* Heading for the Image Section */
.image-carousel h2 {
  text-align: left;
  font-size: 3rem;
  font-weight: bold;
  color: #2b2727;
  margin-bottom: 10px;
  margin-left: 0;
  padding-left: 0;
  width: 90%;
}

/* Horizontal Scrolling Images Section */
.scrolling-images {
  display: flex;
  gap: 20px;
  overflow-x: auto;
  overflow-y: hidden;
  padding: 5px 0;
  scroll-behavior: smooth;
  width: 90%;
  justify-content: flex-start;
  box-sizing: border-box;
  scrollbar-width: thin;
  scrollbar-color: #855E42 #f8ecd7;
}

/* Individual Image Card */
.image-card {
  min-width: 250px;
  max-width: 250px;
  flex-shrink: 0;
  border-radius: 8px;
  overflow: hidden;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  transition: transform 0.3s ease-in-out;
}

/* Hover Effect for Image Card */
.image-card:hover {
  transform: scale(1.05);
}

/* Image inside the Card */
.carousel-image {
  width: 100%; 
  height: 80%;
  border-radius: 8px;
}

/* Image Details Section */
.image-details {
  padding: 10px;
  text-align: center;
  background-color: #f8ecd7;
}

/* Image Details Title */
.image-details h3 {
  font-size: 1.5rem;
  font-weight: bold;
  margin: 10px 0;
  color: #855E42;
}

/* Image Details Description */
.image-details p {
  font-size: 1.4rem;
  color: #666;
  margin: 0;
}

/* Responsiveness for Smaller Screens */
@media (min-width: 1200px) {
  .image-card {
    min-width: calc(25% - 20px);
  }
}

@media (max-width: 1199px) and (min-width: 768px) {
  .image-card {
    min-width: calc(33.33% - 20px);
  }
}

@media (max-width: 767px) {
  .image-card {
    min-width: calc(50% - 20px);
  }
}

@media (max-width: 768px) {
  .image-carousel h2 {
    font-size: 1.5rem;
  }

  .scrolling-images {
    gap: 15px;
  }

  .image-details h3 {
    font-size: 1rem;
  }

  .image-details p {
    font-size: 0.9rem;
  }
}

@media (max-width: 480px) {
  .scrolling-images {
    gap: 10px;
  }

  .image-details h3 {
    font-size: 0.9rem;
  }

  .image-details p {
    font-size: 0.8rem;
  }
}

/* Custom Scrollbar for Webkit Browsers */
.scrolling-images::-webkit-scrollbar {
  width: 5px;
}

.scrolling-images::-webkit-scrollbar-track {
  background: #f8ecd7;
}

.scrolling-images::-webkit-scrollbar-thumb {
  background-color: #855E42;
  border-radius: 10px;
}

.scrolling-images::-webkit-scrollbar-thumb:hover {
  background-color: #7a4e3a;
}

.newsection {
  width: 100%;
  margin: auto;
  text-align: center;
  padding: 15px;
  box-sizing: border-box;
  background-color: #f8ecd7;
}

/* Header Styling */
.header {
  text-align: left;
  margin-bottom: 0;
  margin-left: 6%; /* Ensure no extra margin on the left */
  padding-left: 12%; 
}

.header h2 {
  font-size: 30px;
}

/* Image Styling */
.image-container {
  display: flex;
  justify-content: center; /* Centers the image horizontally */
  margin-bottom: auto;
  background-color: #f8ecd7;
}

.image {
  width: 65%; /* You can adjust this width as needed, e.g., 50%, 70%, 100% */
  height: auto;
  object-fit: cover; /* Ensures image covers the space without distortion */
  display: block; /* Removes any inline-block space */
}

/* Below Grid Styling */
.below-grid h4 {
  margin: 5px 0;
}

/* Individual Heading Adjustments */
.heading1 {
  font-size: 55px;
  color: rgb(7, 7, 7);
  text-align: center;
}

.heading2 {
  font-size: 80px;
  font-family: Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
  color: rgb(0, 0, 0);
  text-align: center;
}

.heading3 {
  font-size: 30px;
  color: rgb(4, 4, 4);
  text-align: center;
}

/* Media Queries for Responsiveness */

/* For larger screens (like laptops, desktops, and large tablets) */
@media (min-width: 764px) {
  .container {
    width: 90%;
  }
}

/* For tablets and smaller devices (portrait mode) */
@media (max-width: 768px) {
  .container {
    width: 90%;
  }
}

/* For mobile screens */
@media (max-width: 480px) {
  .header h2 {
    font-size: 30px;
  }
}

.site-footer {
  background-color: #f8ecd7;
  padding: 1rem 1rem;
  margin-top: 0;
  border-top: 2px solid #855E42;
}

.footer-sections {
  max-width: 1200px;
  margin: 0 auto;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 2rem;
  padding: 0 1rem;
}

.footer-section {
  display: flex;
  flex-direction: column;
}

.footer-heading-expl, 
.footer-heading-hlp, 
.footer-heading-cmp {
  font-size: 2rem;
  font-weight: 500;
  margin-bottom: 1rem;
  color: #7f4720;
}

.footer-list {
  list-style: none;
  padding: 0;
  margin: 0;
}

.footer-list-item {
  margin-bottom: 0.75rem;
}

.footer-link {
  color: #707072;
  text-decoration: none;
  font-size: 1.2rem;
  transition: color 0.2s ease;
}

.footer-link:hover {
  color: #111;
}

@media (max-width: 768px) {
  .footer-sections {
    grid-template-columns: 1fr;
    gap: 2rem;
  }

  .footer-section {
    padding: 0 1rem;
  }

  .site-footer {
    padding: 2rem 1rem;
  }
}
